//状态管理vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js'
import actions from './actions.js'

Vue.use(Vuex);


const store=new Vuex.Store({
    //状态数据
    state:{
        //主题分类数据
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'测试'},
          ],
          //未登录时的菜单
          unLoginedMoreMenus:{
            login:'登录'
          },
          loginedMoreMenus:{
            center:'个人中心',
            create:'发表新话题',
            msg:'消息通知',
            logout:'登出'
          },
          //判断用户是否登录
          isLogined:false, 
          //登录用户名
          loginname:'',
          //登录用户的accesstoken
          accesstoken:'',
          //登录用户id
          author_id:'',
          //是否显示更多菜单
          isShowMoreMenus:false  
    },
    //状态数据计算属性
    getters:{
      moreMenus:function(state){
        if(state.isLogined){
          return state.loginedMoreMenus;
        }
          return state.unLoginedMoreMenus;
      },
      getTopicDescByTab:function(state){
        //返回函数结构
        return function(tab){
          for(var index=0;index<state.topics.length;index++){
            if(state.topics[index].name == tab){
              return state.topics[index].desc;
            }
          }
        };
      }
    },
    mutations,
    actions
})

export default store;



